@keyframes smartphoto {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes smartphoto-img-wrap {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes smartphoto-inner {
    from {
        transform: translate(0, 100px);
    }

    to {
        transform: translate(0, 0);
    }
}

@keyframes smartphoto-loader {
    0% {
        opacity: 0.4;
        transform: rotate(0deg);
    }

    50% {
        opacity: 1;
        transform: rotate(180deg);
    }

    100% {
        opacity: 0.4;
        transform: rotate(360deg);
    }
}

@keyframes smartphoto-appear {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

@keyframes smartphoto-hide {
    0% {
        display: block;
        opacity: 1;
    }

    99% {
        display: block;
        opacity: 0;
    }

    100% {
        display: none;
        opacity: 0;
    }
}

.smartphoto {
    position: fixed;
    z-index: 9999;
    top: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: black;
    opacity: 1;
    font-family: sans-serif;
    cursor: pointer;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -ms-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
    transition: all 0.3s ease-out;
    animation-name: smartphoto;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;

    &[aria-hidden="true"] {
        display: none;
    }
}

.smartphoto-close {
    opacity: 0;
}

.smartphoto-count {
    display: inline-block;
    color: $white;
    font-size: 16px;
}

.smartphoto-header {
    display: block;
    box-sizing: border-box;
    position: fixed;
    z-index: 102;
    top: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 50px;
    padding: 15px;
    background-color: $black-2;
}

.smartphoto-content {
    display: block;
    position: absolute;
    top: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 100%;
}

.smartphoto-dismiss {
    display: block;
    position: absolute;
    top: 15px;
    inset-inline-end: 10px;
    width: 20px;
    height: 20px;
    padding: 0;
    border: none;
    background-color: transparent;
    background-image: url(data:image/svg+xml;base64,);
    text-shadow: 0 1px 0 $white;
    color: $white;
    font-size: 30px;
    text-decoration: none;
    cursor: pointer;
    line-height: 1;
}

.smartphoto-body {
    position: relative;
    z-index: 102;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.smartphoto-inner {
    position: relative;
    width: 100%;
    height: 100%;
    vertical-align: top;
}

.smartphoto-img {
    display: none;
    max-width: none;
    width: auto;
    height: auto;
    cursor: zoom-in;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: transform 0.3s ease-out;
    -webkit-user-drag: none;

    &.active {
        display: block;
    }
}

.smartphoto-img-onmove {
    cursor: grab;
    cursor: -webkit-grab;
    transition: none;
}

.smartphoto-img-elasticmove {
    transition: transform 0.3s ease-out;
}

.smartphoto-img-wrap {
    display: inline-block;
    opacity: 1;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -ms-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
    animation-name: smartphoto-img-wrap;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;
}

.smartphoto-img-left {
    transform: translateX(150%) !important;
}

.smartphoto-img-right {
    transform: translateX(-150%) !important;
}

.smartphoto-arrows {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 1002;
    top: 50%;
    inset-inline-start: 0;
    opacity: 1;
    animation-name: smartphoto-appear;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;

    &[aria-hidden="true"] {
        animation-name: smartphoto-hide;
        display: none;
    }

    li {
        display: block;
        position: absolute;
        top: 50%;
        width: 30px;
        height: 30px;
        margin-block-start: -20px;
        box-sizing: content-box;
        animation-duration: 0.3s;
        animation-timing-function: ease-out;
        animation-name: smartphoto-appear;

        &:focus {
            outline: none;
        }
    }

    [aria-hidden="true"] {
        animation-name: smartphoto-hide;
        display: none;
    }

    a {
        display: block;
        width: 100%;
        height: 100%;
        text-decoration: none;
    }
}

.smartphoto-arrow-right {
    inset-inline-end: 0;
    padding: 5px 0;
    background-color: $black-5;

    a {
        background-image: url(data:image/svg+xml;base64,);
    }
}

.smartphoto-arrow-left {
    inset-inline-start: 0;
    padding: 5px 0;
    background-color: $black-5;

    a {
        background-image: url(data:image/svg+xml;base64,);
    }
}

.smartPhotoArrowHideIcon {
    display: none;
}

.smartphoto-nav {
    position: absolute;
    bottom: 0;
    inset-inline-start: 0;
    width: 100%;
    opacity: 1;
    animation-name: smartphoto-appear;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;

    &[aria-hidden="true"] {
        animation-name: smartphoto-hide;
        display: none;
    }

    ul {
        display: block;
        overflow-x: auto;
        list-style: none;
        margin: 0;
        padding: 0;
        text-align: center;
        white-space: nowrap;
        -webkit-overflow-scrolling: touch;
    }

    li {
        display: inline-block;
        overflow: hidden;
        width: 50px;
        height: 50px;
    }

    a {
        display: block;
        width: 100%;
        height: 100%;
        background-color: $white;
        background-position: center center;
        background-size: cover;
        opacity: 0.5;

        &:focus {
            opacity: 0.8;
        }

        &.current {
            opacity: 1 !important;
        }
    }

    img {
        width: auto;
        height: 100%;
    }
}

.smartphoto-list {
    list-style-type: none;
    position: absolute;
    z-index: 101;
    top: 0;
    inset-inline-start: 0;
    margin: 0;
    padding: 0;
    white-space: nowrap;

    li {
        display: block;
        position: absolute;
        top: 0;
        inset-inline-start: 0;
        width: 100%;
        height: 100%;
        transition: all 0.3s ease-out;

        &:focus {
            outline: none;
        }
    }
}

.smartphoto-list-onmove {
    transition: all 0.3s ease-out;
}

.smartphoto-caption {
    overflow: hidden;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 50px;
    padding: 0 50px;
    color: $white;
    font-size: 12px;
    text-align: center;
    line-height: 50px;
    white-space: nowrap;
    text-overflow: ellipsis;

    &:focus {
        outline: none;
    }
}

.smartphoto-loader-wrap {
    display: block;
    position: relative;
    z-index: 103;
    width: 0;
    height: 0;
    transform: translate(50vw, 50vh);
}

.smartphoto-loader {
    position: absolute;
    z-index: 101;
    top: 0;
    inset-inline-start: 0;
    width: 30px;
    height: 30px;
    margin-block-start: -25px;
    margin-inline-start: -25px;
    border: 8px solid #17cddd;
    border-inline-end-color: transparent;
    border-radius: 50%;
    animation: smartphoto-loader 0.5s infinite linear;
}

.smartphoto-img-clone {
    position: fixed;
    z-index: 100;
    top: 0;
    inset-inline-start: 0;
    transition: all 0.3s ease-out;
}

.smartphoto-sr-only {
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0, 0, 0, 0);
}

.sidebar {
    box-shadow: none !important;
}



.masonry .brick {
    width: auto;
    margin-block-end: 20px;
}

.brick img {
    border-radius: 5px;
}

.smartphoto-img.active {
    border-radius: 5px;
}

.smartphoto-arrows li {
    padding: 8px;
    border-radius: 5px;
    background: var(--primary02);
}

.smartphoto-arrows a {
    width: 50% !important;
    height: 50% !important;
    margin: 7px;
}

.smartphoto-dismiss {
    width: 15px !important;
    height: 15px !important;
    inset-inline-end: 22px !important;
    top: 18px !important;
}

.smartphoto {
    background-color: $black-8 !important;
}

.smartphoto-arrow-left {
    inset-inline-start: 15px !important;
}

.smartphoto-arrow-right {
    inset-inline-end: 15px !important;
}

.smartphoto-nav li {
    width: 70px !important;
    height: 70px !important;
    margin-inline-start: 5px;
}

.smartphoto-nav {
    bottom: 10px !important;
}

.smartphoto-nav a {
    border-radius: 5px;
    opacity: 0.6 !important;
    border: #ededf5;
}

.smartphoto-list li {
    display: table !important;
}