.intl-tel-input {
    position: relative;
    border-color: #e1e6f1;

    .hide {
        display: none;
    }

    .flag-dropdown {
        position: absolute;
        z-index: 1;
        cursor: pointer;
        height: 40px;
        line-height: 40px;

        .selected-flag {
            margin: 1px 2px 1px 1px;
            margin-block: 1px;
            margin-inline-end: 2px;
            margin-inline-start: 1px;
            padding-block: 11px;
            padding-inline-start: 6px;
            padding-inline-end: 16px;
            background: #f2f5ff;

            &:hover {
                background-color: #eaeef9;
            }
        }
    }
}

.country-list .highlight {
    float: inherit;
}

.intl-tel-input {
    .flag-dropdown {
        .selected-flag .down-arrow {
            top: 6px;
            position: relative;
            inset-inline-start: 20px;
            width: 0;
            height: 0;
            border-inline-start: 4px solid transparent;
            border-inline-end: 4px solid transparent;
            border-block-start: 4px solid black;
        }

        .country-list {
            list-style: none;
            padding: 0;
            margin: 0;
            z-index: 1;
            overflow-y: scroll;
            box-shadow: 1px 1px 4px rgb(209, 212, 231);
            background-color: white;
            border: 0;
            position: absolute;
            top: 42px;
            width: 250px;
            max-height: 200px;
            box-shadow: 0px 12px 41px 0px rgba(108, 119, 138, 0.3);

            .divider {
                padding-block-end: 5px;
                margin-block-end: 5px;
                border-block-end: 1px solid #eaf0ff;
            }

            .country {
                line-height: 16px;
                padding: 10px 10px;
                font-size: 14px;

                .dial-code {
                    color: #9097b5;
                }

                &.highlight {
                    background-color: rgb(238, 242, 253);
                    font-size: 14px;
                    border: 0;
                    border-radius: 0;
                    margin: 0;
                }
            }

            .flag {
                display: inline-block;
                vertical-align: bottom;
                margin-inline-end: 6px;
            }

            .country-name {
                margin-inline-end: 6px;
            }
        }
    }

    input {
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        height: 40px;
        padding-inline-start: 47px;
        position: relative;
        z-index: 0;
        border: 1px solid #e1e6f1;
        width: 250px;

        &:focus {
            outline: none;
        }
    }
}

